<template>
  <div class="home">
    <div class="block main marginTop wow slideInLeft">
      <!-- 小标题组件 -->
      <title-box title="最新发布" class="title-box wow slideInLeft" data-wow-delay="0.4s"></title-box>
      <!-- 文章列表组件 -->
      <blog-list :AllArticle="AllArticle"></blog-list>
    </div>
  </div>
</template>

<script>
import WOW from 'wowjs';
import BlogList from '../components/BlogList/BlogList.vue'
import TitleBox from '../components/TitleBox/titleBox.vue'
export default {
  name: 'Home',
  components: {
    BlogList,//文章列表组件
    TitleBox,//标题组件
  },
  data() {
    return {
      //获取最新发布6篇文章
      AllArticle:[],
    }
  },
  methods:{
    // 获取最新发布6篇文章
    GetAllArticle(){
      this.$http.get('/blog/selectNewBlog').then(res => {
        // 获取最新发布6篇文章
        this.AllArticle = res.data.data.newBlog
      })
    },
  },
  created(){
    this.GetAllArticle()
  },
  mounted(){
      	let wow = new WOW.WOW({
          boxClass: 'wow',
          animateClass: 'animated',
          offset: 0,
          mobile: true,
          live: true
        });
        wow.init();
    }
}
</script>

